import { Component, OnInit } from '@angular/core';
import { AdminSideMenu } from "@enum/app_enum";
import { ClassService } from "@core/services/class.service";
import { Router } from '@angular/router';

@Component({
    selector: 'app-parent-class',
    templateUrl: './parent-class.component.html',
    styleUrls: ['./parent-class.component.css']
})
export class ParentClassComponent implements OnInit {
    public search: String;
    public page: any = 1;
    public direction: String;

    public routeDirLists: any = {
        routeName: [AdminSideMenu.Class],
        routeLink: []
    };
    public classList: any = {};

    constructor(private _service: ClassService, private _router: Router) { }

    ngOnInit(): void {
        this.getClassList();
    }


    getClassList() {
        this._service.classLists(this.search, this.direction, this.page)
            .subscribe((res) => {
                this.classList = res;
            });
    }

    goToDetail(classId) {
        this._router.navigateByUrl("parent/classes/management/detail/" + classId)

    }
}
