import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { HomeworkService, ClassService } from "@core/services/serviceIndex";
import { AuthenticationService } from "@core/authentication/authentication.service";
import { Role } from "@enum/app_enum";
import { Subscription } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from "@env/environment";
@Component({
  selector: 'app-home-work',
  templateUrl: './home-work.component.html',
  styleUrls: ['./home-work.component.css']
})
export class HomeWorkComponent implements OnInit,OnDestroy {
  @Input() classId: String;
  @Input() teacherData: any;

  public isHWCreate: boolean = false;
  public hwIndex: any;

  public userole: any;
  public role: any = Role;
  public file: any;
  public homework: any = [];
  public isLoading: boolean = false;
  public hwValue: any;
  public replyValue: any;
  public replyFile: any;
  
  public isImageLoad: boolean = true;
  public subscription: Subscription;

  constructor(private _service: HomeworkService, private _auth: AuthenticationService, private _classService: ClassService,private http: HttpClient) { }

  ngOnInit(): void {
    this.isLoading = true;
    this.userole = this._auth.currentUserValue.role;
    this.getHomeWork()
  }

  getHomeWork() {
    this.subscription = this._service.getHomework(this.classId).subscribe((res) => {
      this.homework = res.homeworks;
      this.isLoading = false;
    })
  }

  createHomework() {
    this.isHWCreate = true;
  }

  gethwValue(val: String) {

    this.hwValue = val;

  }

  createHw() {
    this.isLoading = true;
    this.subscription = this._classService.createHomework(this.classId, this.hwValue)
      .subscribe((res) => {
        this.isHWCreate = false;
        this.getHomeWork();
      })
  }

  createHwF() {
    console.log(this.file);


      // upload$.subscribe();
    this.isLoading = true;
    this.subscription = this._classService.createHomeworkF(this.classId, this.file)
      .subscribe((res) => {
        this.isHWCreate = false;
        this.getHomeWork();
      })
  }

  getHWId(id: String) {
    if (this.hwIndex) {
      this.hwIndex = null;
      this.replyValue = null;
    }

    else
      this.hwIndex = id;
  }

  getReplyValue(val: String) {
    this.replyValue = val;
  }

  createReply(id: String) {
    this.isLoading = true;
    this.subscription = this._classService.createReply(this.classId, id, this.replyValue)
      .subscribe((res) => {
        this.hwValue = null;
        this.hwIndex = null;
        this.getHomeWork();
      })
  }

  createReplyF(id: String) {
    this.isLoading = true;
    this.subscription = this._classService.createReplyF(this.classId, id, this.replyFile)
      .subscribe((res) => {
        this.hwValue = null;
        this.hwIndex = null;
        this.getHomeWork();
      })
  }

  onLoad() {
    this.isImageLoad = false;
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

  getfile(file: any) {
    this.file = file;
  }
  
  getreplyfile(file: any) {
    this.replyFile = file;
  }
}
